.stepWrapper{
    width: 100%;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.stepContent{
    width: 100%;
    max-width: 750px;
    min-height: 200px;
    text-align: center;
    background-color: antiquewhite;
    border: 2px dashed rgba(0, 0, 0, 0.384);
    border-radius: 7px;
    margin-top: 16px;
    display: flex;
    justify-content: center;
    align-items: stretch;
    flex-direction: column;
}

.steps{
    max-width: 750px;
}

.buttonWrapper {
    width: 100%;
    max-width: 750px;
    justify-content: right;
    justify-content: space-between;
    align-items: center;
    display: flex;
}

.nextButton, .previousButton {
    width: auto;
    height: auto;
    background-color: transparent;
    box-shadow: none;
    border: none;
}

.nextButton:hover, .previousButton:hover{
    background-color: transparent !important;
    transform: scale(1.1);
}

.nextButtonArrow, .previousButtonArrow{
    font-size: 50px;
    color: rgb(78, 76, 76);
}